@use '../../abstracts/variables/colors' as *;
@use '../../abstracts/variables/size' as *;
@use '../../abstracts/mixins/mixins' as *;

header {
    position: sticky;
    top: -43px;
    z-index: 1;
    margin-bottom: 18px;

    &::before {
        content: "";
        position: absolute;
        background: $light-bg;
        width: 100%;
        left: 0;
        top: -16px;
        height: 100%;
        box-shadow: 0 0 5px #0000001a;
    }

    .mycontainer {
        position: relative;
        padding: 16px 32px;

        .myrow.one {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            padding-bottom: 16px;
            border-bottom: 1px solid $border-color-light;

            .box-logo {
                display: flex;
                align-items: center;
            }            

            .box-menu {

                ul {
                    display: flex;
                    flex-wrap: wrap;
                    // gap: 12px;
                    margin: 0;

                    li {
                        margin: 0;

                        &:nth-child(2) {
                            margin-left: 12px;
                        }

                        .get-pro {
                            cursor: pointer;
                            border-radius: 8px;
                            border: 2px solid rgba(255, 255, 255, 0.12);
                            border: none;
                            background: $primary-color;
                            color: $light-color;
                            font-size: 14px;
                            font-weight: 600;
                            line-height: $line-height;
                            outline: none;
                            padding: 10px 16px;
                            margin: 0;
                            // box-shadow: 0 0 0 1px rgba(10, 13, 18, 0.18) inset, 0 -2px 0 0 rgba(10, 13, 18, 0.05) inset, 0 1px 2px 0 rgba(10, 13, 18, 0.05);
                        
                            &:hover {
                                background: $primary-hover-color;
                                background: #023FDC;
                            }
                        }
                    }
                }
            }
        }

        .myrow.two {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: center;
            padding-top: 20px;
            padding-bottom: 20px;

            .box-navigation {
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                margin-top: 0;

                ul {
                    display: flex;
                    flex-wrap: wrap;
                    gap: 12px;
                    margin: 0;

                    li {
                        margin: 0;
                    }
                }
            }

            .box-save-changes {
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                justify-content: space-between;
                gap: 12px;
            }
        }
    }
}

@media only screen and (max-width: 768px) {

    header {
        margin-bottom: 0;

        &::before {
            top: -15px;
        }

        .mycontainer {
            padding: 15px;

            .myrow.one,
            .myrow.two {
                gap: 15px;
            }

            .myrow.one {
                padding-bottom: 15px;
            }

            .myrow.two {
                padding-top: 15px;
                padding-bottom: 15px;

                .box-navigation {
                    overflow: auto;

                    ul {
                        flex-wrap: nowrap;
                    }
                }
            }
        }
    }
}